import { Component, OnInit } from '@angular/core';
import { BlueomegaadminServiceService } from '../blueomegaadmin-service.service';
import { switchMap } from 'rxjs/operators';
import { ActivatedRoute, Params } from '@angular/router';
import { Project } from '../project';

@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.css'],
  providers: [BlueomegaadminServiceService]
})
export class HomepageComponent implements OnInit {

  projects: Project[] = [];

  public newProject: Project = {
    _id: '',
    name: '',
    technology: '',
    platform: '',
    image: '',
    developer: '',
    status: '',
    version: '',
    releaseyear: 0
  };

  constructor(private ProjectService: BlueomegaadminServiceService, private route: ActivatedRoute) { }

  ngOnInit(): void {
    this.ProjectService.getProjects()
      .then(results => {
        this.projects = results;
      });

    this.route.params.pipe(
      switchMap((params: Params) => {
        return this.ProjectService.getSingleProject(params.projectid);
      })
    )
      .subscribe((newProject: Project) => {
        console.log('Selected Project', newProject);
        this.newProject = newProject;
        this.pageContent.header.title = this.newProject.name;
        this.pageContent.header.body = "Details for the selected project."
      });
  }

  pageContent = {
    header: {
      title: '',
      body: ''
    }
  };

  public deleteProject(newProject: Project): void {
    this.ProjectService.deleteProject(newProject._id);
    window.location.href = '';
  }

  public createNewProject(newProject: Project): void {
    this.ProjectService.createProject(newProject);
  }

}
