import { Component, OnInit } from '@angular/core';
import { BlueomegaadminServiceService } from '../blueomegaadmin-service.service';
import { switchMap } from 'rxjs/operators';
import { ActivatedRoute, Params } from '@angular/router';
import { Member } from '../member';

@Component({
  selector: 'app-members',
  templateUrl: './members.component.html',
  styleUrls: ['./members.component.css'],
  providers: [BlueomegaadminServiceService]
})
export class MembersComponent implements OnInit {

  members: Member[] = [];

  public newMember: Member = {
    _id: '',
    fname: '',
    lname: '',
    employee_image: '',
    gender: '',
    email: '',
    phone: 0,
    technology: '',
    platform: '',
    employee_id: '',
    position: '',
    experience: '',
    joiningyear: 0
  };

  constructor(private memberService: BlueomegaadminServiceService, private route: ActivatedRoute) { }

  ngOnInit(): void {

    this.route.params.pipe(
      switchMap((params: Params) => {
        return this.memberService.getSingleMember(params.memberid);
      })
    )
      .subscribe((newMember: Member) => {
        this.newMember = newMember;
        this.pageContent.header.title = this.newMember.fname;
        this.pageContent.header.body = "Details for the selected project."
      });

    this.memberService.getMembers()
      .then(results => {
        this.members = results;
      });
  }

  pageContent = {
    header: {
      title: '',
      body: ''
    }
  };

  public deleteMember(newMember: Member): void {
    this.memberService.deleteMember(newMember._id);
    window.location.href = '';
  }

  public createNewMember(newMember: Member): void {
    this.memberService.createMember(newMember);
  }

}
